/* testing new features */


.portfolio__item {
    background: var(--color-bg);
    padding: 2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
    position: relative;
    perspective: 1000px;
    border: 1px solid #fff;
    height: 20rem;
    width: 35rem;

}
.front, .back {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1.5s;
    backface-visibility: hidden;
    background-color: black;
}
.front img {
    max-width: 100%;
    min-width: 100%;
    height: auto;
    filter: grayscale(100%)
}
.back {
    transform: rotateY(180deg);
}
.portfolio__item-image:hover .background__image{
    opacity: 0.5;
}
.portfolio__item-image:hover .front {
    transform: rotateY(180deg);
    /* height: 50rem;
    width: 50rem; */
}
.portfolio__item-image:hover .back {
    transform: rotateY(360deg);
    opacity: 0.8;
    /* height: 50rem;
    width: 50rem; */
}

.portfolio__item h3{
    margin: 1.2rem 0 2rem;
}
.portfolio__item-cta{
    display: flex;
    gap: 1rem;
    margin-bottom:1rem ;
}


/* Media queries (Medium devices) */
@media screen and (max-width: 1024px){
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
}
/* Media queries (Small devices) */
@media screen and (max-width: 600px)
{
    .portfolio__container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .portfolio__item{
        margin-top: 1rem;
    }
    
}
    
    



